import { Component, OnInit } from "@angular/core";
import { DiscoverProvidersService } from "./discover-providers.service";
import { environment } from "../../../environments/environment";
import { ToastrService } from "ngx-toastr";
import { discoverProviders } from "../../const";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-discover-providers",
	templateUrl: "./discover-providers.component.html",
	styleUrls: ["./discover-providers.component.css"],
})
export class DiscoverProvidersComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	server_url = environment.server_url;
	challengeSector: any = [];
	providers: any;
	query: any = {
		slug: {},
		filter: {},
		search: {
			name: "",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	searchKeyWord: any = "";
	autoSuggestions: any;
	searchLoader: boolean = false;
	profileImageLoaded: boolean = true;
	sortByValues: any = [];
	providerRoleValues: any = [];
	teamSizeValues: any = [];
	providerLocationsValues: any;
	sortValue: any = "created_at DESC";
	teamSizeFilterClauses: any = [];
	LocationIds: any = [];
	entityType: any = [];
	providerIdBasedonLocations: any = [];
	providersIdBasedOnTeamsize: any = [];
	providerIdsBasedonChallengeSector: any = [];

	constructor(private _provider: DiscoverProvidersService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	ngOnInit() {
		this.sortByValues = discoverProviders.sort_by_values;
		this.providerRoleValues = discoverProviders.provider_role;
		this.teamSizeValues = discoverProviders.team_size_options;
		this.listProviders();
		this.listProviderLocations();
	}

	async listProviders() {
		try {
			this.searchLoader = true;
			this.providers = await this._provider.discoverProviders(this.query);
			this.searchLoader = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async listProviderLocations() {
		try {
			this.providerLocationsValues = await this._provider.getLocations();
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async filterSector(event) {
		try {
			if (event.target.checked) {
				this.challengeSector.push(parseInt(event.target.value));
			} else {
				this.challengeSector.splice(this.challengeSector.indexOf(parseInt(event.target.value)), 1);
			}
			this.query.filter.sector = this.challengeSector;
			if (_.isEmpty(this.challengeSector)) delete this.query.filter.sector;
			this.query.pg.skip = 0;
			this.listProviders();
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	filterEntity(event) {
		if (event.target.checked) {
			this.entityType.push(event.target.value);
		} else {
			this.entityType.splice(this.entityType.indexOf(event.target.value), 1);
		}
		this.query.filter.role = this.entityType;
		if (_.isEmpty(this.entityType)) delete this.query.filter.role;
		this.query.pg.skip = 0;
		this.listProviders();
	}

	async filterTeamSize(event) {
		let from = event.target.value.split("-")[0];
		let to = event.target.value.split("-")[1];
		if (event.target.checked) {
			this.teamSizeFilterClauses.push({ from: parseInt(from), to: parseInt(to) });
		} else {
			let itemToBeRemoved = { from: parseInt(from), to: parseInt(to) };
			let deleteIndex = this.teamSizeFilterClauses.findIndex((a) => a.from == itemToBeRemoved.from);
			this.teamSizeFilterClauses.splice(deleteIndex, 1);
		}
		this.query.filter.teamsize = this.teamSizeFilterClauses;
		if (_.isEmpty(this.teamSizeFilterClauses)) delete this.query.filter.teamsize;
		this.query.pg.skip = 0;
		this.listProviders();
	}

	async filterLocation(event) {
		try {
			if (event.target.checked) {
				this.LocationIds.push(parseInt(event.target.value));
			} else {
				this.LocationIds.splice(this.LocationIds.indexOf(parseInt(event.target.value)), 1);
			}
			this.query.filter.location = this.LocationIds;
			if (_.isEmpty(this.LocationIds)) delete this.query.filter.location;
			this.query.pg.skip = 0;
			this.listProviders();
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	searchProvider() {
		this.autoSuggestions = null;
		this.query.pg.skip = 0;
		this.listProviders();
	}

	changeLimit() {
		this.query.pg.skip = 0;
		this.listProviders();
		window.scrollTo(0, 0);
	}

	handlePagination(page) {
		this.query.pg.skip = (page - 1) * this.query.pg.limit;
		this.listProviders();
	}

	sortResults(event) {
		this.query.sort = event.target.value;
		this.listProviders();
	}

	intersectArray(x: any, y: any) {
		if (_.isEmpty(x)) return y;
		else if (_.isEmpty(y)) return x;
		else return _.intersection(x, y);
	}

	async autoComplete(keyword) {
		this.query.search.name = keyword;
		if (keyword.length > 3) {
			this.autoSuggestions = await this._provider.getSearchSuggestions({
				search: {
					name: keyword,
				},
			});
		} else if (keyword.length == 0) {
			this.searchProvider();
		} else {
			this.autoSuggestions = null;
		}
	}

	searchAutoSuggestion(keyword) {
		this.query.search.name = keyword;
		this.searchKeyWord = keyword;
		this.searchProvider();
	}
}
