import { Component, OnInit } from "@angular/core";
import { discover_challenges_provider } from "../../const";
import { DiscoverChallengesService } from "./discover-challenges.service";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import * as _ from "lodash";

@Component({
	selector: "app-discover-challenge-provider",
	templateUrl: "./discover-challenge-provider.component.html",
	styleUrls: ["./discover-challenge-provider.component.css"],
})
export class DiscoverChallengeProviderComponent implements OnInit {
	post_data: any = {
		slug: {},
		 filter: {
			status: "active",
			uns_proposal:false
		},
		search: {
			name: "",
		},
		sort: ["created_at DESC"],
		pg: {
			limit: 10,
			skip: 0,
		},
	};

	autoSuggestions: any;
	challengeSector: any = [];
	challenges: any;
	search_term: any;
	sort_value: any = "created_at DESC";
	anchors_options: any = [];
	solution_options: any = [];
	team_size_options: any = [];
	sort_by_values: any = [];
	search: any = {};
	searchLoader: boolean = false;
	anchoredCities: any;
	smartCityIds: any = [];
	anchorIdBasedOnSmartCity: any = [];
	anchorFilterClauses: any[] = [];
	solutionFilterClauses: any[] = [];

	constructor(private _dc: DiscoverChallengesService, private toastr: ToastrService, private router: Router) {}

	ngOnInit() {
		this.anchors_options = discover_challenges_provider.anchors_options;
		this.solution_options = discover_challenges_provider.solution_options;
		this.team_size_options = discover_challenges_provider.team_size_options;
		this.sort_by_values = discover_challenges_provider.sort_by_values;
		this.getChallenges();
		this.listAchoredCities();
	}

	async getChallenges() {
		try {
			this.searchLoader = true;
			this.challenges = await this._dc.getChallenges(this.post_data);
			// console.log(this.challenges)
			this.searchLoader = false;
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async listAchoredCities() {
		try {
			this.anchoredCities = await this._dc.listCities();
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	sort(event) {
		this.post_data.sort = [event.target.value];
		this.getChallenges();
	}

	solutionFilter(event) {
		let from = event.target.value.split("-")[0];
		let to = event.target.value.split("-")[1];
		if (event.target.checked) {
			this.solutionFilterClauses.push({ from: parseInt(from), to: parseInt(to) });
		} else {
			let itemToBeRemoved = { from: parseInt(from), to: parseInt(to) };
			let deleteIndex = this.solutionFilterClauses.findIndex((s) => s.from == itemToBeRemoved.from);
			this.solutionFilterClauses.splice(deleteIndex, 1);
		}
		this.post_data.filter.by_solution = this.solutionFilterClauses;
		if (_.isEmpty(this.solutionFilterClauses)) delete this.post_data.filter.by_solution;
		this.getChallenges();
	}

	anchorFilter(event) {
		let from = event.target.value.split("-")[0];
		let to = event.target.value.split("-")[1];
		if (event.target.checked) {
			this.anchorFilterClauses.push({ from: parseInt(from), to: parseInt(to) });
		} else {
			let itemToBeRemoved = { from: parseInt(from), to: parseInt(to) };
			let deleteIndex = this.anchorFilterClauses.findIndex((a) => a.from == itemToBeRemoved.from);
			this.anchorFilterClauses.splice(deleteIndex, 1);
		}
		this.post_data.filter.by_anchor = this.anchorFilterClauses;
		if (_.isEmpty(this.anchorFilterClauses)) delete this.post_data.filter.by_anchor;
		this.getChallenges();
	}

	searchChallenge() {
		this.autoSuggestions = null;
		this.post_data.pg.skip = 0;
		this.getChallenges();
	}

	async filterSmartCity(event) {
		if (event.target.checked) {
			this.smartCityIds.push(parseInt(event.target.value));
		} else {
			this.smartCityIds.splice(this.smartCityIds.indexOf(parseInt(event.target.value)), 1);
		}
		try {
			this.searchLoader = true;
			this.anchorIdBasedOnSmartCity = await this._dc.getChallenegesBasedOnSmartCities({
				filter: {
					frg_smart_city_id: this.smartCityIds,
				},
			});
			if (_.isEmpty(this.anchorIdBasedOnSmartCity)) this.anchorIdBasedOnSmartCity = [0];
			this.post_data.filter.by_anchor_id = this.anchorIdBasedOnSmartCity;
			if (_.isEmpty(this.smartCityIds)) delete this.post_data.filter.by_anchor_id;
			this.post_data.pg.skip = 0;
			this.challenges = await this._dc.getChallenges(this.post_data);
			this.searchLoader = false;
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	filterSector(event) {
		if (event.target.checked) {
			this.challengeSector.push(parseInt(event.target.value));
		} else {
			this.challengeSector.splice(this.challengeSector.indexOf(parseInt(event.target.value)), 1);
		}
		this.post_data.filter.sector = this.challengeSector;
		if (_.isEmpty(this.challengeSector)) delete this.post_data.filter.sector;
		this.post_data.pg.skip = 0;
		this.getChallenges();
	}

	handlePagination(pageIndex) {
		this.post_data.pg.skip = (pageIndex - 1) * this.post_data.pg.limit;
		this.getChallenges();
	}

	async autoComplete(keyword) {
		if (keyword.length > 3) {
			this.autoSuggestions = await this._dc.getSearchSuggestions({
				search: { name: keyword },
				sort: [{ name: "ASC" }],
			});
		} else if (keyword.length == 0) {
			// console.log("auto suggestion cleared");
			this.searchChallenge();
		} else {
			this.autoSuggestions = null;
		}
	}

	searchAutoSuggestion(keyword) {
		this.post_data.search.name = keyword;
		this.searchChallenge();
	}

	openRouteInNewTab(path) {
		const url = this.router.serializeUrl(this.router.createUrlTree([`/challenge/${path}`]));
		window.open(url, "_blank");
	}
}
