import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import * as _ from "lodash";
import { HelpersService } from "../../services/helpers.service";

@Injectable({
	providedIn: "root",
})
export class DiscoverChallengesService {
	api_url = environment.api_url;
	constructor(private http: HttpClient, private _hs: HelpersService) {}

	async getChallenges(post_data: any) {
		const limit = parseInt(post_data.pg.limit);
		const page = parseInt(post_data.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + "/challenges/discover", post_data).toPromise();
		let totalResults = dataResponse.total_records;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(totalResults / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async listCities() {
		return this.http.post(this.api_url + "/smart_cities/list", {}).toPromise();
	}

	async getChallenegesBasedOnSmartCities(cityIds) {
		let response: any = await this.http.post(this.api_url + "/anchors_and_smart_cities/select_mul", cityIds).toPromise();
		let challengeIds = response.data.map((e) => e.frg_anchor_id);
		return challengeIds;
	}

	async getSearchSuggestions(keyword: any) {
		let response: any = await this.http.post(this.api_url + "/challenges/select_mul", keyword).toPromise();
		let suggestions = [];
		response.data.forEach((e) => {
			suggestions.push(e.name);
		});
		//  console.log(suggestions);
		let bestMatches = [];
		this._hs.sortBestMatches(keyword.search.name, suggestions).forEach((element) => {
			bestMatches.push(element.target);
		});
		//  console.log(bestMatches);
		return bestMatches;
	}
}
