import { Component, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";
declare var $:any;

@Component({
	selector: "app-create-challenge-btn-modal",
	templateUrl: "./create-challenge-btn-modal.component.html",
	styleUrls: ["./create-challenge-btn-modal.component.css"],
})
export class CreateChallengeBtnModalComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	@ViewChild("closebutton", { static: false }) closebutton;
	loggedinuserEmail: any;
	userRole: string;

	constructor(private router: Router) {
		this.loggedInUser$.subscribe((resp) => {
			this.userRole = resp.LoggedInUser.userType;
		});
	}

	ngOnInit() {}

	createChallenge() {
		this.router.navigateByUrl(`/challenge/create`);
		this.closebutton.nativeElement.click();
	}
	gotoChallenge(){
		$('#createChallenge').modal('hide');
		this.router.navigateByUrl(`/discover/challenges`);
	}
	gotoUnsolicitedProposal(){
		this.router.navigateByUrl(`/provider/unsolicited-proposal`);
	}
}
