import { Component, OnInit } from "@angular/core";
import { environment } from "../../../environments/environment";
import { DiscoverSolutionsService } from "./discover-solutions.service";
import { discoverSolutions } from "../../const";
import { ToastrService } from "ngx-toastr";
import * as _ from "lodash";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-discover-offerings",
	templateUrl: "./discover-offerings.component.html",
	styleUrls: ["./discover-offerings.component.css"],
})
export class DiscoverOfferingsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	server_url = environment.server_url;
	solutions: any;
	searchLoader: boolean = false;
	solutionMaturity: any = [];
	challengeSector: any = [];
	sortByValues: any = [];
	solutionMaturityValues: any = [];
	teamSizeValues: any = [];
	sort_value: any = "created_at DESC";
	providerLocations: any;
	teamSize: any = [];
	LocationIds: any = [];
	providerIdBasedonLocations: any = [];
	providersIdBasedOnTeamsize: any = [];
	solutionIdBasedOnSector: number[] = [];
	query: any = {
		slug: {},
		filter: {},
		search: {
			name: "",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	autoSuggestions: any;
	teamSizeFilterClauses: any = [];

	constructor(private _solutions: DiscoverSolutionsService, private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	ngOnInit() {
		// console.log("child component");
		this.sortByValues = discoverSolutions.sort_by_values;
		this.solutionMaturityValues = discoverSolutions.solution_maturity;
		this.teamSizeValues = discoverSolutions.team_size_options;
		this.listSolutions();
		this.listProviderLocations();
	}

	async listSolutions() {
		try {
			this.searchLoader = true;
			this.solutions = await this._solutions.discoverSolutions(this.query);
			this.solutions.data.map((e) => (e.images = JSON.parse(e.images)));
			this.searchLoader = false;
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async listProviderLocations() {
		try {
			this.providerLocations = await this._solutions.getLocations();
		} catch (err) {
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	sortResults(event) {
		this.query.sort = event.target.value;
		this.listSolutions();
	}

	async filterSector(event) {
		if (event.target.checked) {
			this.challengeSector.push(parseInt(event.target.value));
		} else {
			this.challengeSector.splice(this.challengeSector.indexOf(parseInt(event.target.value)), 1);
		}
		this.query.filter.sector = this.challengeSector;
		if (_.isEmpty(this.challengeSector)) delete this.query.filter.sector;
		this.query.pg.skip = 0;
		this.listSolutions();
	}

	filterSolutionStage(event) {
		if (event.target.checked) {
			this.solutionMaturity.push(event.target.value);
		} else {
			this.solutionMaturity.splice(this.solutionMaturity.indexOf(event.target.value), 1);
		}
		this.query.filter.stage = this.solutionMaturity;
		if (_.isEmpty(this.solutionMaturity)) delete this.query.filter.stage;
		this.query.pg.skip = 0;
		this.listSolutions();
	}

	async filterLocation(event) {
		if (event.target.checked) {
			this.LocationIds.push(parseInt(event.target.value));
		} else {
			this.LocationIds.splice(this.LocationIds.indexOf(parseInt(event.target.value)), 1);
		}
		this.query.filter.location = this.LocationIds;
		if (_.isEmpty(this.LocationIds)) delete this.query.filter.location;
		this.query.pg.skip = 0;
		this.listSolutions();
	}

	async filterTeamSize(event) {
		let from = event.target.value.split("-")[0];
		let to = event.target.value.split("-")[1];
		if (event.target.checked) {
			this.teamSizeFilterClauses.push({ from: parseInt(from), to: parseInt(to) });
		} else {
			let itemToBeRemoved = { from: parseInt(from), to: parseInt(to) };
			let deleteIndex = this.teamSizeFilterClauses.findIndex((a) => a.from == itemToBeRemoved.from);
			this.teamSizeFilterClauses.splice(deleteIndex, 1);
		}
		this.query.filter.teamsize = this.teamSizeFilterClauses;
		if (_.isEmpty(this.teamSizeFilterClauses)) delete this.query.filter.teamsize;
		this.query.pg.skip = 0;
		this.listSolutions();
	}

	searchSolutions() {
		this.autoSuggestions = null;
		this.query.pg.skip = 0;
		this.listSolutions();
	}

	handlePagination(pageIndex) {
		this.query.pg.skip = (pageIndex - 1) * this.query.pg.limit;
		this.listSolutions();
	}

	async autoComplete(keyword) {
		if (keyword.length > 3) {
			this.autoSuggestions = await this._solutions.getSearchSuggestions({
				search: { name: keyword },
			});
		} else if (keyword.length == 0) {
			this.searchSolutions();
		} else {
			this.autoSuggestions = null;
		}
	}

	searchAutoSuggestion(keyword) {
		this.query.search.name = keyword;
		this.searchSolutions();
	}
}
