import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from "@angular/core";
import { FilterService } from "./filter.service";
import * as _ from "lodash";
import { OwlOptions } from 'ngx-owl-carousel-o';
// import Swiper core and required modules

@Component({
	selector: "app-sector-filter",
	templateUrl: "./sector-filter.component.html",
	styleUrls: ["./sector-filter.component.css"],
})
export class SectorFilterComponent implements OnInit, OnChanges {
	@Output() onFilterChange = new EventEmitter<any>();
	@Input() sectors: any;
	isLoading: boolean;
	sectorsData: any;
	
	// OwlCarousel Options
	customOptions: OwlOptions = {
		loop: false,
		mouseDrag: false,
		touchDrag: false,
		pullDrag: false,
		dots: false,
		navSpeed: 700,
		nav: true,
		navText:["<i class='fa fa-angle-left'></i>", "<i class='fa fa-angle-right'></i>"],
		// navText: ['', ''],
		responsive: {
		0: {
			items: 1
		},
		400: {
			items: 4
		},
		740: {
			items: 6
		},
		940: {
			items: 12
		}
		},		
  } 

	constructor(private _filter: FilterService) {}

	ngOnInit() {
		this.getSectors();
	}

	ngOnChanges() {}

	async getSectors() {
		this.isLoading = true;
		this.sectorsData = await this._filter.fetchSector();
		this.sectorsData.data.map((sector) => {
			sector.checked = false;
		});
		this.isLoading = false;
	}

	handleSectorFilter(event) {
		this.onFilterChange.emit(event);
	}

	
 
}
