import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { HelpersService } from "../../services/helpers.service";
import * as _ from "lodash";

@Injectable({
	providedIn: "root",
})
export class DiscoverProvidersService {
	api_url = environment.api_url;

	constructor(private http: HttpClient, private _hs: HelpersService) {}

	async discoverProviders(postData) {
		const limit = parseInt(postData.pg.limit);
		const page = parseInt(postData.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + "/providers/discover", postData).toPromise();
		let totalResults = dataResponse.total_records;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(totalResults / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getSearchSuggestions(keyword) {
		let response: any = await this.http.post(this.api_url + "/providers/search_auto_suggestions", keyword).toPromise();
		let suggestions = [];
		response.data.forEach((element) => {
			if (element.enterprise_company_name) {
				suggestions.push(element.enterprise_company_name);
			} else if (element.startup_company_name) {
				suggestions.push(element.startup_company_name);
			} else if (!element.enterprise_company_name && !element.startup_company_name) {
				suggestions.push(element.fullname);
			}
		});
		// console.log(suggestions);
		let bestMatches = [];
		this._hs.sortBestMatches(keyword.search.name, suggestions).forEach((element) => {
			bestMatches.push(element.target);
		});
		// console.log(bestMatches);
		return bestMatches;
	}

	async getLocations() {
		return await this.http.post(this.api_url + "/locations/list", {}).toPromise();
	}

	async getProviderIdBasedOnChallengeSector(challengeSectorIds) {
		let response: any = await this.http.post(this.api_url + "/challenge_sectors_and_providers/select_mul", challengeSectorIds).toPromise();
		let providerIDs = [];
		response.data.forEach((provider) => {
			providerIDs.push(provider.frg_provider_id);
		});
		return providerIDs;
	}

	async getProvidersBasedOnLocations(locations) {
		let response: any = await this.http.post(this.api_url + "/locations_and_providers/select_mul", locations).toPromise();
		let providerIDs = [];
		response.data.forEach((provider) => {
			providerIDs.push(provider.frg_provider_id);
		});
		return providerIDs;
	}
}
