import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DiscoverComponent } from './discover.component';
import { DiscoverOfferingsComponent } from './discover-offerings/discover-offerings.component';
import { DiscoverProvidersComponent } from './discover-providers/discover-providers.component';
import { DiscoverChallengeProviderComponent} from './discover-challenge-provider/discover-challenge-provider.component';
import { SelectSectorComponent } from './select-sector/select-sector.component';

const routes: Routes = [
  { path: '', 
    component: DiscoverComponent,
    children:[
      {
        path: '',
        redirectTo: 'challenges',
        pathMatch:'full'
      },
       {
        path: 'select-sector',
        component: SelectSectorComponent,
      },
      {
        path: 'challenges',
        component: DiscoverChallengeProviderComponent,
        data: { reuseRoute: true },
      },
      {
        path: 'solutions',
        component: DiscoverOfferingsComponent,
        data: { reuseRoute: true },
      },
      {
        path: 'providers',
        component: DiscoverProvidersComponent,
        data: { reuseRoute: true },
      },
    ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DiscoverRoutingModule { }
