import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { HelpersService } from "../../services/helpers.service";

@Injectable({
	providedIn: "root",
})
export class DiscoverSolutionsService {
	api_url = environment.api_url;

	constructor(private http: HttpClient, private _hs: HelpersService) {}

	async discoverSolutions(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + "/solutions/discover", query).toPromise();
	
		let totalResults = dataResponse.total_records;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(totalResults / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getSearchSuggestions(keyword) {
		let response: any = await this.http.post(this.api_url + "/solutions/select_mul", keyword).toPromise();
		let suggestions = [];
		response.data.forEach((element) => {
			suggestions.push(element.name);
		});
		let bestMatches = [];
		this._hs.sortBestMatches(keyword.search.name, suggestions).forEach((element) => {
			bestMatches.push(element.target);
		});
		return bestMatches;
	}

	async getLocations() {
		return await this.http.post(this.api_url + "/locations/list", {}).toPromise();
	}

	async getProvidersBasedOnLocations(locations) {
		let response: any = await this.http.post(this.api_url + "/locations_and_providers/select_mul", locations).toPromise();
		let providerIDs = [];
		response.data.forEach((provider) => {
			providerIDs.push(provider.frg_provider_id);
		});
		return providerIDs;
	}

	async getProvidersBasedOnTeamsize(query) {
		let response: any = await this.http.post(this.api_url + "/providers/select_mul", query).toPromise();
		let providerIDs = [];
		response.data.forEach((provider) => {
			providerIDs.push(provider.id);
		});
		return providerIDs;
	}

	async getSolutionIdBasedOnChallengeSector(sectorIds: number[]) {
		let response: any = await this.http
			.post(this.api_url + "/solutions_challenge_sectors/select_mul", {
				filter: {
					frg_challenge_sector_id: sectorIds,
					status: 'active'
				},
			})
			.toPromise();
		let solutionIds = response.data.map((e) => e.frg_solution_id);
		return solutionIds;
	}
}
